.counter{background-color:#D1D5D8;color:#ffffff;display:inline-block;font-size:2rem;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.counter input,.counter button{display:inline-block;vertical-align:middle}.counter input{text-align:center;width:50px}.counter button{outline:none;padding:.5em;-webkit-transition:-webkit-transform 60ms ease-out;transition:-webkit-transform 60ms ease-out;transition:transform 60ms ease-out;transition:transform 60ms ease-out, -webkit-transform 60ms ease-out;width:50px}.counter button:active{-webkit-transform:translateY(2px);transform:translateY(2px);-webkit-transition:-webkit-transform 100ms ease-out;transition:-webkit-transform 100ms ease-out;transition:transform 100ms ease-out;transition:transform 100ms ease-out, -webkit-transform 100ms ease-out}.counter--primary{background-color:#E04938}



